import React, { Component } from "react";
import {
  List,
  NavBar,
  Icon,
  Pagination,
  Button,
  Toast,
  InputItem
} from "antd-mobile";

const Item = List.Item;
const Brief = Item.Brief;

import { get } from "util/fetch";
import "./index.less";
import { setValue } from "@/redux/collection-word";
import { connect } from "react-redux";

const routePath = "/api/english/regular";
const imageHost = "https://jiguangdanci.easylesson.cn";
let playCount = 0;
const locale = {
  prevText: "Prev",
  nextText: "Next"
};
const colorTheme = {
  0: "#e84118",
  1: "#00a8ff",
  2: "#fbc531",
  3: "#4cd137",
  4: "#487eb0",
  5: "#9c88ff"
};
const getUrlVars = url => {
  var vars = {};
  var parts = url.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m, key, value) {
    vars[key] = value;
  });
  return vars;
};
const alphabet = "abcdefghijklmnopqrstuvwxyz".split("");

class index extends Component {
  state = {
    word: this.props.redux.list[0],
    sentence_sound: {},
    phonetic_sound: {},
    collection_words: this.props.redux.list,
    current: 1,
    spelling: "",
    spell_button: [],
    selected_letters: [],
    isTip: false
  };
  componentDidMount() {
    this.spellCell.focus();
    this.getButtonItems(this.state.word.word);
    let button_sound = new Audio(
      "http://img.tukuppt.com/origin_music/00/85/00/mp3-5b9c6af66c768107.mp3"
    );
    let phonetic_sound = new Audio(this.state.word.pronunciation_url);
    phonetic_sound.addEventListener(
      "ended",
      function() {
        if (playCount >= 1) {
          playCount = 0;
        } else {
          playCount++;
          this.play();
        }
      },
      false
    );
    phonetic_sound.play();
    this.setState({
      button_sound,
      phonetic_sound
    });
    // this.init();
  }
  init = () => {
    let query = this.props.location.search;
    let collection_id = getUrlVars(query)["collection_id"];
    log(collection_id);
    get(`${routePath}/${collection_id}`)
      .then(res => {
        log(res);
        let sentence_sound = new Audio(
          imageHost + "/" + res.data[0].sample_sentence_audio
        );
        let phonetic_sound = new Audio(
          imageHost + "/" + res.data[0].pronunciation_url
        );

        let button_sound = new Audio(
          "http://img.tukuppt.com/origin_music/00/85/00/mp3-5b9c6af66c768107.mp3"
        );
        this.setState({
          sentence_sound,
          phonetic_sound,
          button_sound,
          word: res.data[0]
        });
        this.getButtonItems(res.data[0].word.word);
      })
      .catch(err => {
        log(err);
      });
    log(11, this.props.redux.list);
    (this.props.redux.list || []).map((item, index) => {
      if (item.id == collection_id) {
        log(11, item);
        this.setState({
          current: index + 1
        });
      }
    });
  };

  gotoLast = () => {
    this.props.history.go(-1);
  };

  sentenceSound = () => {
    this.state.sentence_sound.playbackRate = 1.5;
    this.state.sentence_sound.play();
  };
  phoneticSound = () => {
    this.state.phonetic_sound.play();
  };
  handlePaginationChange = current => {
    log(111, current);
    if (current >= this.props.redux.list.length) {
      Toast.info("已达终点", 2);
      // return;
    }
    let phonetic_sound = new Audio(
      this.props.redux.list[current - 1].pronunciation_url
    );
    phonetic_sound.addEventListener(
      "ended",
      function() {
        if (playCount >= 1) {
          playCount = 0;
        } else {
          playCount++;
          this.play();
        }
      },
      false
    );

    phonetic_sound.play();
    let button_sound = new Audio(
      "http://img.tukuppt.com/origin_music/00/85/00/mp3-5b9c6af66c768107.mp3"
    );
    this.setState({
      current,
      phonetic_sound,
      button_sound,
      selected_letters: [],
      spelling: "",
      word: this.props.redux.list[current - 1]
    });
    this.getButtonItems(this.props.redux.list[current - 1].word);
    log(current);
  };

  getButtonItems = word => {
    log(1212, word);
    let wordChars = word.split("");
    let start = Math.floor(Math.random() * (26 - 15 + word.length));
    let end = start + 15 - word.length;
    let temp = alphabet.slice(start, end);
    let spell_button = temp.concat(wordChars).sort(() => 0.5 - Math.random());
    this.setState({
      spell_button
    });
    log(start, end, word.length, temp, spell_button);
  };

  handleClickButton = index => {
    let {
      selected_letters,
      spell_button,
      spelling,
      current,
      button_sound
    } = this.state;
    selected_letters.push(index);
    spelling = spelling + spell_button[index];
    log(this.state.word);
    this.state.button_sound.play();
    this.setState({
      selected_letters,
      spelling
    });
    if (spelling === this.state.word.word) {
      Toast.info("拼写成功", 1, () => {
        this.handlePaginationChange(current + 1);
      });
    }
  };
  handleSpell = e => {
    let spelling = e;
    this.setState({
      spelling
    });
    // log(spelling);
    if (spelling === this.state.word.word) {
      Toast.info("拼写成功", 1, () => {
        this.handlePaginationChange(this.state.current + 1);
      });
    }
  };
  deleteLetter = () => {
    this.state.button_sound.play();
    let { selected_letters, spell_button, spelling } = this.state;

    selected_letters.pop();
    spelling = spelling.slice(0, -1);
    // log(selected_letters);
    this.setState({
      selected_letters,
      spelling
    });
  };

  showTip = () => {
    let phonetic_sound = new Audio(this.state.word.pronunciation_url);
    phonetic_sound.addEventListener(
      "ended",
      function() {
        if (playCount >= 1) {
          playCount = 0;
        } else {
          playCount++;
          this.play();
        }
      },
      false
    );

    phonetic_sound.play();
    this.spellCell.focus();
    this.setState({ isTip: true });
    setTimeout(() => {
      this.setState({ isTip: false });
      this.spellCell.focus();
    }, 3000);
  };
  render() {
    const navBar = (
      <NavBar
        style={{
          backgroundColor: colorTheme[this.state.current % 5],
          color: "#fff"
        }}
        mode="light"
        onLeftClick={this.gotoLast}
        leftContent={<Icon type="left" />}
        rightContent={[<Icon key="1" type="ellipsis" />]}
      >
        拼写单词
      </NavBar>
    );
    const pagination = (
      <div className="pagination">
        <Pagination
          total={(this.props.redux.list || []).length}
          current={this.state.current}
          locale={locale}
          onChange={this.handlePaginationChange}
        />
      </div>
    );
    log(111, this.state.spell_button);
    const content = (
      <div className="content">
        <div className="image">
          <img src={this.state.word.profile_picture_url} alt="" />
          <div
            className="word"
            style={{
              backgroundColor: colorTheme[this.state.current % 5],
              display: this.state.isTip ? "block" : "none"
            }}
          >
            {this.state.word.word}
          </div>
          <div
            className="phonetic"
            style={{ backgroundColor: colorTheme[this.state.current % 5] }}
          >
            {this.state.word.phonetic_symbol}
          </div>
          <img
            style={{ backgroundColor: colorTheme[this.state.current % 5] }}
            className="sound-image"
            src="/images/sound.png"
            alt=""
            onClick={this.phoneticSound}
          />
        </div>

        <div className="cell">{this.state.word.definition}</div>

        <div className="spell-title">
          <span>拼写</span>
          {/* <span className="text">{this.state.spelling}</span> */}
          <InputItem
            className="spell"
            ref={input => {
              this.spellCell = input;
            }}
            value={this.state.spelling}
            onChange={this.handleSpell}
          />
          <div>
            <img src="/images/delete.png" alt="" onClick={this.deleteLetter} />
          </div>
        </div>
        <div className="spell-control">
          {this.state.spell_button.map((item, index) => (
            <Button
              className="button"
              disabled={this.state.selected_letters.includes(index)}
              key={JSON.stringify(item) + index}
              onClick={() => this.handleClickButton(index)}
            >
              {item}
            </Button>
          ))}
        </div>
      </div>
    );
    const tip = (
      <div onClick={this.showTip} className="tip">
        提示
      </div>
    );
    return (
      <div className="train-keke-container">
        {navBar}
        {/* {colorBar} */}
        {content}
        {pagination}
        {tip}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  redux: state.teacherQuestion
});
const mapDispatchToProps = dispatch => {
  return {
    setValue: value => {
      dispatch(setValue(value));
    }
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(index);
